  loadGLTF = () => {

  	var myThis = this;

	this.loader = new GLTFLoader();
	const dracoLoader = new DRACOLoader();
	dracoLoader.setDecoderPath( 'three/examples/js/libs/draco/gltf/' );
	this.loader.setDRACOLoader(dracoLoader );
	const ddsLoader = new DDSLoader();
	this.loader.setDDSLoader(ddsLoader );

	var loadStartTime = performance.now();
	this.loader.load( 'textures/BoomBoxLight.gltf', function( data ) {

		myThis.gltf = data;

		var object = myThis.gltf.scene;

		console.info( 'Load time: ' + ( performance.now() - loadStartTime ).toFixed( 2 ) + ' ms.' );

		object.traverse( function( node ) {

			if ( node.material && ( node.material.isMeshStandardMaterial ||
				 ( node.material.isShaderMaterial && node.material.envMap !== undefined ) ) ) {

				node.material.envMap = myThis.envMap;
				node.material.needsUpdate = true;
			}

		} );

		object.traverse( function ( node ) {

			// if ( node.isMesh || node.isLight ) node.castShadow = true;

		} );
		object.rotation.y -= Math.PI;
		myThis.scene.add( object );
		

		var scale = gltfScale;
		myThis.meshGLTF = object.children[0]
		myThis.meshGLTF.frustumCulled = false
		// myThis.meshGLTF.visible = guiData.currentMesh === 'gltf'
		myThis.meshGLTF.geometry.scale(scale,scale,scale)

		myThis.materialGLTF = myThis.meshGLTF.material
		decorateMaterialWithPerMapTransforms(myThis.materialGLTF)
		// myThis.applyInstancingToMesh2(myThis.meshGLTF)
		myThis.applyInstancingToMeshExtended(myThis.meshGLTF,2)
		myThis.meshArr[5] = myThis.meshGLTF;
		// myThis.applyIndecisToInstancedMesh(myThis.meshGLTF)

	}, undefined, function ( error ) {

		console.error( error );

	} );

}

