  startAnimationLoop = () => {
  	
 	
		this.targetX = this.mouseX * .001;
		this.targetY = this.mouseY * .001;

		var delta = this.clock.getDelta() * 0.01 * 1;
		if ( this.mesh ) {

			if (isBrowser) {
				this.mesh.rotation.y += 0.05 * ( this.targetX - this.mesh.rotation.y );
				this.mesh.rotation.x += 0.05 * ( this.targetY - this.mesh.rotation.x );
			}

			this.mesh.material.time += delta;
			if(this.mesh.selected){
				// console.log("it's selected");
				// if(this.mesh.material.scale<5){
				// 	this.mesh.material.scale += delta*300;
				// }
				// this.mesh.material.userData.uScale.value = this.mesh.material.scale;
			}else{
				// console.log("it's not selected");
				// if(this.mesh.material.scale>2.5){
				// 	this.mesh.material.scale -= delta*300;
				// }
				// this.mesh.material.userData.uScale.value = this.mesh.material.scale;
			}
			
			this.mesh.material.userData.uTime.value = this.mesh.material.time;
			
			this.mesh.material.needsUpdate = true;
			// console.log("color", this.mesh.material.scale);
			

		}
		if ( this.mesh4 ) {

			if (isBrowser) {
				this.mesh4.rotation.y += 0.05 * ( this.targetX - this.mesh4.rotation.y );
				this.mesh4.rotation.x += 0.05 * ( this.targetY - this.mesh4.rotation.x );
			}

			this.mesh4.material.time += delta
			this.mesh4.material.userData.uTime.value = this.mesh4.material.time;
			this.mesh4.material.needsUpdate = true;
			

		}
		if ( this.mesh5 ) {

			if (isBrowser) {
				this.mesh5.rotation.y += 0.05 * ( this.targetX - this.mesh5.rotation.y );
				this.mesh5.rotation.x += 0.05 * ( this.targetY - this.mesh5.rotation.x );
			}

			this.mesh5.rotation.y = this.mesh3.rotation.y + Math.PI;
			this.mesh5.material.time += delta
			this.mesh5.material.userData.uTime.value = this.mesh5.material.time;
			this.mesh5.material.needsUpdate = true;
			

		}
		if ( this.mesh2 ) {

			if (isBrowser){
				this.mesh2.rotation.y += 0.05 * ( this.targetX - this.mesh2.rotation.y );
				this.mesh2.rotation.x += 0.05 * ( this.targetY - this.mesh2.rotation.x );
			}

			this.mesh2.material.time += delta
			this.mesh2.material.userData.uTime.value = this.mesh2.material.time;
			this.mesh2.material.needsUpdate = true;
			

		}
		if ( this.mesh3 ) {

			if (isBrowser){
				this.mesh3.rotation.y += 0.05 * ( this.targetX - this.mesh3.rotation.y );
				this.mesh3.rotation.x += 0.05 * ( this.targetY - this.mesh3.rotation.x );
			}

			this.mesh3.material.time += delta
			this.mesh3.material.userData.uTime.value = this.mesh3.material.time;
			this.mesh3.material.needsUpdate = true;
			

		}

		if ( this.meshGLTF ) {

			if (isBrowser){
				this.meshGLTF.rotation.y += 0.05 * ( this.targetX - this.meshGLTF.rotation.y );
				this.meshGLTF.rotation.x += 0.05 * ( this.targetY - this.meshGLTF.rotation.x );
			}

			this.meshGLTF.material.time += delta;

		}
		if ( this.meshGLTF2 ) {

			if (isBrowser){
				this.meshGLTF2.rotation.y += 0.05 * ( this.targetX - this.meshGLTF2.rotation.y );
				this.meshGLTF2.rotation.x += 0.05 * ( this.targetY - this.meshGLTF2.rotation.x );
			}

			this.meshGLTF2.material.time += delta;

		}
	    this.renderer.render(this.scene, this.camera);
	    this.stats.end();

    // The window.requestAnimationFrame() method tells the browser that you wish to perform
    // an animation and requests that the browser call a specified function
    // to update an animation before the next repaint
    this.requestID = window.requestAnimationFrame(this.startAnimationLoop);

  };

